.container {
  max-width: 1920px;
  margin: 0 auto; }

@media (min-width: 768px) and (max-width: 1919px) {
  .container {
    max-width: 1919px; } }

@media (min-width: 340px) and (max-width: 767px) {
  .container {
    max-width: 767px; } }

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  display: none;
  width: 100%;
  height: 100%; }

.overlay--open {
  display: initial; }

.header {
  display: flex;
  justify-content: space-between; }

.header__lang {
  display: flex;
  justify-content: space-between;
  align-items: center; }

.header__settings p {
  margin-top: 10px;
  font-weight: bold;
  font-size: 22px; }

.header__ru {
  padding-right: 5px;
  border-right: 1px solid #e4e4e4; }

.header__green {
  color: #69a54d; }

.header__green--mail {
  margin-top: 63px;
  font-size: 20px; }

.header__lang p {
  margin-left: 40px;
  margin-bottom: 10px;
  font-weight: normal; }

.header-menu {
  display: flex;
  justify-content: space-between;
  padding-left: 0;
  font-weight: bold;
  list-style-type: none; }

.navigation {
  background-color: #7f9d64; }

.navigation p {
  font-size: 26px; }

.navigation__list a {
  color: #ffffff; }

.breadcrumps {
  display: flex;
  justify-content: flex-start;
  margin-top: 62px;
  padding-left: 0;
  color: #69a54d;
  list-style-type: none; }

.breadcrumps a {
  color: #69a54d;
  text-decoration: none; }

.breadcrumps__item {
  margin-left: 4px;
  margin-right: 4px; }

.breadcrumps__item--active a {
  color: #cacaca; }

.breadcrumps__item--arrow {
  color: #cacaca; }

.nav-list {
  display: flex;
  justify-content: space-between;
  padding-top: 19px;
  padding-bottom: 19px;
  padding-left: 0;
  list-style-type: none; }

.nav-list__item img {
  padding-right: 24px; }

.nav-list a {
  font-size: 20px;
  color: #ffffff;
  text-decoration: none; }

.main h1 {
  margin-top: 43px;
  margin-bottom: 30px;
  font-size: 48px;
  line-height: 58px; }

.main--index h1 {
  margin: 200px auto 50px;
  font-size: 108px;
  text-align: center;
  opacity: 0.7; }

.main__catalog {
  padding-left: 0;
  list-style-type: none; }

.main h2 {
  margin-top: 67px;
  margin-bottom: 44px;
  font-size: 40px; }

.presentation {
  color: #ffffff;
  background-color: #809ba5;
  background-image: url("../../img/index-bg.png");
  background-repeat: no-repeat;
  background-size: 100%; }

.presentation p {
  max-width: 685px;
  text-align: center; }

.slider {
  display: flex;
  justify-content: space-between;
  align-items: center; }

.slider p {
  font-size: 96px;
  opacity: 0.7; }

.slider__swing a:nth-child(2) {
  margin-left: 25px;
  margin-right: 25px; }

.agriculture h2 {
  font-size: 48px;
  text-align: center; }

.agriculture p {
  font-size: 18px; }

.elevator-list {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  padding-left: 0;
  list-style-type: none; }

.elevator-list li {
  position: relative;
  margin-bottom: 30px;
  min-width: 400px; }

.elevator-list__link {
  position: absolute;
  content: "";
  top: 20px;
  left: 20px;
  display: block;
  width: 50px;
  height: 20px;
  color: #ffffff;
  font-size: 22px;
  z-index: 3; }

.elevator-list__pic {
  position: absolute;
  content: "";
  bottom: 40px;
  left: 20px;
  display: block;
  width: 39px;
  height: 21px;
  color: #ffffff;
  z-index: 3; }

.elevator-list__bg {
  position: relative; }

.elevator-list__bg::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.39);
  width: 100%;
  height: 100%;
  z-index: 2; }

.elevator-list__bg img {
  display: block;
  min-height: 100%; }

.agriculture-list {
  display: flex;
  justify-content: space-between;
  padding-left: 0;
  list-style-type: none; }

.agriculture-list__item {
  max-width: 235px; }

.agriculture-list__item span {
  font-size: 24px; }

.agriculture-list__item p:first-child {
  font-size: 80px;
  color: #cdbd84; }

.boilers p {
  font-size: 18px; }

.boilers-list {
  display: flex;
  justify-content: space-between;
  padding-left: 0;
  list-style-type: none; }

.boilers-list li {
  position: relative; }

.boilers-list__link {
  position: absolute;
  content: "";
  top: 20px;
  left: 20px;
  display: block;
  width: 39px;
  height: 62px;
  color: #ffffff;
  font-size: 22px;
  font-weight: bold;
  z-index: 3; }

.boilers-list__arrow {
  position: absolute;
  content: "";
  bottom: 10px;
  left: 20px;
  display: block;
  color: #ffffff;
  z-index: 3; }

.boilers-list__bg {
  position: relative; }

.boilers-list__bg::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.39);
  width: 100%;
  height: 100%;
  z-index: 2; }

.boilers-list__bg img {
  display: block;
  min-height: 100%; }

.news-list {
  display: flex;
  justify-content: space-between;
  padding-left: 0;
  list-style-type: none; }

.news-list h3 {
  max-width: 375px;
  font-size: 22px;
  color: #69a54d; }

.news-list__date {
  font-size: 14px;
  color: #cac1a0; }

.news-list__desc {
  max-width: 475px; }

.production-list {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  padding-left: 0;
  list-style-type: none; }

.production-list h3 {
  color: #69a54d;
  font-size: 22px; }

.production-list li {
  position: relative;
  max-width: 50%; }

.production-list__text {
  max-width: 564px; }

.production-list__pic {
  min-width: 700px; }

.production-list__number {
  position: absolute;
  content: "";
  bottom: 110px;
  right: 38px;
  display: block;
  width: 77px;
  height: 88px;
  color: #ffffff;
  opacity: 0.7;
  font-size: 72px; }

.goods__album {
  display: flex;
  justify-content: space-between;
  padding-bottom: 75px; }

.goods__description {
  margin-left: 66px; }

.goods__description p {
  margin-top: 0;
  margin-bottom: 30px; }

.goods__buy a:first-child {
  padding: 10px 87px 14px;
  margin-right: 27px;
  color: #ffffff;
  border-radius: 5px;
  background-color: #7f9d64; }

.goods__buy a:nth-child(2) {
  padding: 11px 45px 13px;
  color: #ffffff;
  border-radius: 5px;
  background-color: #cdbd84; }

.goods-album__mini {
  display: flex;
  justify-content: space-between;
  margin-top: 33px; }

.goods-desc__price {
  font-weight: bold;
  font-size: 20px; }

.goods-desc__price span {
  font-size: 24px; }

.characteristics {
  padding-bottom: 112px; }

.characteristics h3 {
  margin-top: 52px;
  margin-bottom: 30px; }

.characteristics__list {
  padding-left: 0;
  list-style-type: none; }

.characteristics p {
  margin-top: 17px;
  margin-bottom: 30px; }

.characteristics__border {
  padding: 26px;
  border: 1px solid #cdbd84;
  box-sizing: border-box; }

.characteristics__table {
  border-collapse: collapse;
  border: 2px solid white; }

.characteristics__table td:nth-child(2) {
  max-width: 270px;
  text-align: left; }

.characteristics__table th,
.characteristics__table td {
  max-width: 100px;
  padding: 5px;
  text-align: center;
  font-size: 14px;
  vertical-align: baseline;
  border: 1px solid #dcdcdc; }

.catalog-item {
  display: flex;
  padding-top: 31px;
  padding-left: 28px;
  padding-bottom: 34px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 30px;
  border: 1px solid #e4e4e4; }

.catalog-item--shadow {
  box-shadow: 0px 4px 20px 0px rgba(0, 0, 0, 0.1); }

.catalog-item img {
  margin-right: 30px; }

.catalog-item__description a {
  display: inline-block;
  margin-top: 37px;
  padding: 10px 45px;
  color: #ffffff;
  background-color: #7f9d64;
  text-decoration: none; }

.tab__tablinks {
  padding: 10px;
  margin-right: 2px;
  background-color: #cdcdcd;
  border: none;
  border-radius: 3px; }

.tab__tablinks:first-child {
  background-color: #cdbd84; }

.pagination {
  display: flex;
  align-items: center;
  padding-left: 0;
  margin: 47px 30px 95px;
  list-style-type: none; }

.pages__list {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 0;
  margin-right: 10px;
  margin-left: 10px;
  font-size: 18px;
  list-style-type: none; }

.pages__list li {
  margin-right: 10px;
  margin-left: 10px; }

.pages__list li:nth-child(2) {
  padding: 5px 10px;
  background-color: #e8deba; }

.footer-menu {
  padding-top: 62px;
  padding-bottom: 62px;
  text-decoration: none; }

.footer-menu__list {
  padding-left: 0;
  list-style-type: none; }

.footer-menu__list a {
  color: #222222;
  text-decoration: none; }

.location {
  padding-top: 42px;
  padding-bottom: 42px; }

.location__item {
  max-width: 280px; }

.contacts {
  display: block;
  padding-top: 75px;
  padding-bottom: 102px;
  text-align: center;
  color: #ffffff;
  background-color: #7f9d64; }

.contacts h2 {
  margin-bottom: 52px;
  font-size: 40px; }

.contacts input {
  width: 30%;
  margin-bottom: 30px;
  margin-right: 30px; }

.contacts input:last-child {
  margin-right: 0; }

.contacts textarea {
  width: 98%; }

.contacts input,
.contacts textarea {
  padding: 13px 19px;
  color: #ffffff;
  background-color: #7f9d64;
  border: 1px solid #ffffff; }

.contacts input::placeholder,
.contacts textarea::placeholder {
  color: #ffffff; }

.contacts button {
  padding: 10px 45px;
  color: #7f9d64;
  background-color: #ffffff;
  border: none; }

.footer {
  display: block; }

.footer__list a {
  color: #222222;
  text-decoration: none; }

.footer__block {
  background-color: rgba(232, 222, 186, 0.28);
  border-bottom: 1px solid #e4e4e4; }

.footer__container {
  display: flex;
  justify-content: space-between; }

.footer__rights {
  display: flex;
  justify-content: space-between;
  padding-top: 25px;
  padding-bottom: 30px; }

.footer__rights p {
  margin: 0; }

body {
  margin: 0;
  padding: 0;
  font-family: "Montserrat", "Helvetica", sans-serif;
  font-size: 16px;
  color: #222222;
  letter-spacing: 0;
  line-height: 30px;
  box-sizing: border-box; }
